import React, { useEffect, useState } from 'react';
import TaskItem from '../components/TaskItem';
import { db } from '../firebase/firebaseConfig';
import { collection, onSnapshot, deleteDoc, doc } from 'firebase/firestore';
//texto de de respaldo a 

function TaskList(){
    const [tasks, setTasks] = useState([]);

    useEffect(() => {
        const unsubscribe = onSnapshot(collection(db, "Orden"), (snapshot) => {
            const tasksData = snapshot.docs.map((doc) => ({
                id: doc.id, ...doc.data() }));
            setTasks(tasksData);
        });

        return () => unsubscribe(); }, []);

    const handleDelete = async (taskId) => {
        try {
            await deleteDoc(doc(db, "Orden", taskId));
        } catch (error) {
            console.error('error al eliminar orden:', error);
        }
    };

    if (tasks.length === 0) {
    return <p>No hay rdenes registradas</p>;
    }

  return (
    <div className="container mt-3">
      {tasks.map((task) => (
        <div 
          key={task.id} 
          className="mb-3"
          style={{ maxWidth: '600px' }} // ancho mximo pero sin centrar
        >
          <TaskItem task={task} onDelete={handleDelete} />
        </div>
      ))}
    </div>
  );
}

export default TaskList;


