import React, { useState } from "react";
import ProductItem from "./ProductItem";

function ProductList({ cart, setCart }) {
  const [products, setProducts] = useState([
    { id: 1, name: "Mouse", price: 5000, stock: 2 },
    { id: 2, name: "Teclado", price: 10000, stock: 1 },
    { id: 3, name: "Audfonos", price: 15000, stock: 3 }
  ]);

  const addToCart = (product) => {
    if (product.stock <= 0) return;

    setCart([...cart, product]);

    setProducts(products.map(p =>
      p.id === product.id
        ? { ...p, stock: p.stock - 1 }
        : p
      ));
  };

  const removeFromCart = (indexToRemove) => {
    const productToRemove = cart[indexToRemove];

    setCart(cart.filter((_, index) => index !== indexToRemove));

    setProducts(products.map(p =>
      p.id === productToRemove.id
        ? { ...p, stock: p.stock + 1 }
        : p
    ));
    };


  return (
    <div className="mt-3">
      <h2>Productos</h2>
      <div className="row">
        {products.map(product => (
          <div key={product.id} className="col-md-4 mb-3">
            <ProductItem
              product={product}
              onAdd={addToCart}
            />
          </div>
        ))}
      </div>

      {cart.length > 0 && (
        <div className="mt-4">
          <h2>Carrito</h2>
          <ul className="list-group">
            {cart.map((item, index) => (
              <li key={index} className="list-group-item d-flex justify-content-between align-items-center">
                {item.name}: ${item.price.toLocaleString('es-CL')}
                <button
                  className="btn btn-danger btn-md"
                  onClick={() => removeFromCart(index)}
                >
                  Quitar
                </button>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}

export default ProductList;