import './App.css';
import React, { useState, useEffect } from 'react';
import TaskForm from './components/TaskForm';
import TaskList from './components/TaskList';
import ProductList from './components/ProductList';
import Login from './components/Login';
import { auth } from './firebase/firebaseConfig';
import { onAuthStateChanged, signOut } from 'firebase/auth';

function ProjectManagementApp() {
  const [showProducts, setShowProducts] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [showList, setShowList] = useState(false);
  const [cart, setCart] = useState([]);
  const [user, setUser] = useState(null);

  //  Escucha sesin activa
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });

    return () => unsubscribe();
  }, []);

  //  No logueado  Login
  if (!user) {
    return <Login />;
  }

  //  Logueado  App
  return (
    <div className="container mt-4">
      {/*  APP CARD GENERAL */}
      <div className="card p-4 shadow">
        {/*  HEADER */}
        <div className="card mb-4">
          <div className="card-body d-flex justify-content-between align-items-center">
            <h4>Bienvenido: {user.email}</h4>

            <button
              className="btn btn-danger btn-md"
              onClick={() => signOut(auth)}
            >
              Cerrar sesin
            </button>
          </div>
        </div>

        {/*  TIENDA */}
        <div className="card mb-4">
          <div className="card-body">
            <h3 className="mb-3">Tienda</h3>

            <button
              className="btn btn-primary mb-3"
              onClick={() => setShowProducts(!showProducts)}
            >
              {showProducts ? 'Ocultar Productos' : 'Mostrar Productos'}
            </button>

            {showProducts && (
              <ProductList cart={cart} setCart={setCart} />
            )}
          </div>
        </div>
            
        {/*  RDENES */}
        <div className="card mb-4">
          <div className="card-body">
            <h3 className="mb-3">Gestin de rdenes</h3>

            <button
              className="btn btn-success mb-3"
              onClick={() => setShowForm(!showForm)}
            >
              {showForm ? 'Ocultar Formulario' : 'Agregar Orden'}
            </button>

            {showForm && (
              <div className="mb-4">
                <TaskForm cart={cart} setCart={setCart} />
              </div>
            )}

            <br/>

            <button
              className="btn btn-primary mb-3"
              onClick={() => setShowList(!showList)}
            >
              {showList ? 'Ocultar rdenes' : 'Mostrar rdenes'}
            </button>

            {showList && (
              <div className="mt-3">
                <TaskList />
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
export default ProjectManagementApp;